*{
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
}

h3 {
    font-family: 'Koulen', cursive;

    font-family: 'Oswald', sans-serif;

    font-family: 'Work Sans', sans-serif;
}
p {
    font-family: 'Oswald', sans-serif;

    font-family: 'Work Sans', sans-serif;
}
.nav_bar {
    width: 100%;
    height: 40px;
    background: #3ab7ff;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo{
    margin: 10px;
    font-size: 20px;
    color: #fff;
}
.logo i{
    color: aqua;
}

.nav {
    display: flex;
}
.nav li {
    margin: 10px;
}
.nav li a {
    font-size: 15px;
    cursor: pointer;
}

img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.load_box {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
}

.load_gap{
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.load_bar {
    width: 5px;
    height: 30px;
    border-radius: 10px;
    background: #3ab7ff;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    transition: all ease-in-out 0.5s;
    -webkit-transition: all ease-in-out 0.5s;
    -moz-transition: all ease-in-out 0.5s;
    -ms-transition: all ease-in-out 0.5s;
    -o-transition: all ease-in-out 0.5s;
}
.lb1{
    animation: pop infinite 0.2s;
    -webkit-animation: pop infinite 0.2s;
}
.lb2{
    animation: pap infinite 0.2s;
    -webkit-animation: pap infinite 0.2s;
}
.lb3{
    animation: pep infinite 0.2s;
    -webkit-animation: pep infinite 0.2s;
}

@keyframes pop {
    0%{height: 25px;}
    50%{height: 70px;}
}
@keyframes pap {
    0%{height: 75px;}
    50%{height: 10px;}
}
@keyframes pep {
    0%{height: 105px;}
    50%{height: 30px;}
}

.Image_Card {
    width: 90%;
    height: auto;
    margin: 10px 5%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.Card {
    width: 400px;
    height: auto;
    margin: 10px;
}

.CardBox {
    width: 100%;
    height: auto;
    overflow: hidden;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}


.CardText {
    width: 100%;
    height: auto;
    padding-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.CardText > span {
    padding: 6px 8px;
    background: #3ab7ff;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    margin: 3px;
    font-family: 'Oswald', sans-serif;
}


.search_box {
    width: 90%;
    height: auto;
    margin: 10px 5%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search {
    width: auto;
    height: auto;
}
.search input[type='text']{
    width: 300px;
    height: 40px;
    padding: 0px 10px;
    border: 2px solid #3ab7ff;
    font-size: 19px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    margin: 0 5px;
}
.search input[type='submit']{
    width: 120px;
    height: 40px;
    border: none;
    outline: none;
    background: #3ab7ff;
    font-size: 18px;
    cursor: pointer;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}